<template>
  <div class="container mw768">
    <form :model="certificate" id="certificate">

      <vue-input v-model="certificate.num" label placeholder="Ð¡ÐµÑÐ¸Ð¹Ð½ÑÐ¹ Ð½Ð¾Ð¼ÐµÑ ÑÐ´Ð¾ÑÑÐ¾Ð²ÐµÑÐµÐ½Ð¸Ñ" iconLeft="tag"></vue-input>

      <vue-select :list="contacts" :selected-item="certificate.contact" item-name="contact" label="ÐÐ¾Ð»Ð½Ð¾Ðµ Ð¸Ð¼Ñ" @select="onSelect" icon="user"></vue-select>

      <vue-select :list="companies" :selected-item="certificate.company" item-name="company" label="Ð£ÑÐµÐ±Ð½Ð¾ Ð¼ÐµÑÐ¾Ð´Ð¸ÑÐµÑÐºÐ¸Ð¹ ÑÐµÐ½ÑÑ" @select="onSelect" icon="building"></vue-select>

      <vue-date v-model="certificate.cert_date" label="ÐÐ°ÑÐ° Ð²ÑÐ´Ð°ÑÐ¸"></vue-date>

      <vue-input v-model="certificate.note" label placeholder="ÐÐ°Ð¼ÐµÑÐºÐ°" iconLeft="comment"></vue-input>

      <div class="field is-grouped is-grouped-centered">
        <div class="control">
          <vue-button text="Ð¡Ð¾ÑÑÐ°Ð½Ð¸ÑÑ" color="primary" @click="submit"></vue-button>
        </div>
        <div class="control">
          <vue-button text="ÐÐ°ÐºÑÑÑÑ" @click="close"></vue-button>
        </div>
        <div class="control">
          <vue-button text="Ð£Ð´Ð°Ð»Ð¸ÑÑ" color="danger" onclick="return confirm('ÐÑ Ð´ÐµÐ¹ÑÑÐ²Ð¸ÑÐµÐ»ÑÐ½Ð¾ ÑÐ¾ÑÐ¸ÑÐµ ÑÐ´Ð°Ð»Ð¸ÑÑ ÑÑÑ Ð·Ð°Ð¿Ð¸ÑÑ?');"></vue-button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import vinput from '@/elements/Input'
import vbutton from '@/elements/Button'
import vselect from '@/elements/Select'
import vdate from '@/elements/Date'
import request from '@/request'

export default {
  name: 'certificate',
  components: {
    'vue-input': vinput,
    'vue-button': vbutton,
    'vue-select': vselect,
    'vue-date': vdate
  },
  data () {
    return {
      title: '',
      certificate: {
        id: 0,
        num: '',
        contact_id: 0,
        contact: {
          id: 0,
          name: ''
        },
        company_id: 0,
        company: {
          id: 0,
          name: ''
        },
        cert_date: '',
        note: ''
      },
      contacts: [{
        id: 0,
        name: ''
      }],
      companies: [{
        id: 0,
        name: ''
      }]
    }
  },
  mounted () {
    this.fetchData()
  },
  methods: {
    onSelect (item, itemName) {
      this.certificate[itemName] = item
      this.certificate[`${itemName}_id`] = item.id
    },
    submit () {
      let url = 'certificates'
      let method = 'POST'
      if (this.$route.params.id !== '0') {
        url = `${url}/${this.$route.params.id}`
        method = 'PUT'
      }
      const values = this.certificate
      request({
        url,
        method,
        mode: 'cors',
        data: JSON.stringify(values)
      })
        .then(() => {
          this.close()
        })
    },
    close () {
      this.$router.push('/certificates')
    },
    delete () {
      // console.log('delete!');
    },
    fetchData () {
      request({
        url: `certificates/${this.$route.params.id}`,
        method: 'GET'
      })
        .then((r) => {
          this.certificate = r.data.certificate
          this.contacts = r.data.contacts
          this.companies = r.data.companies
          this.isLoaded = true
        })
    }
  }
}
</script>

<style scoped>
.columns {
  margin-bottom: -0.25rem !important;
}

.field .is-grouped {
  margin-bottom: 0 !important;
}

.w300 {
  width: 300px !important;
}
</style>



